.gallery {
  display: flex;
  flex-wrap: wrap;
}

.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.gallery__item {
  padding: 24px 20px 25px;
  border-bottom: 1px solid #e5e5e5;
}

.gallery__title {
  font-weight: 700;
}

.gallery__text {
  margin: 10px 0;
  text-transform: none;
}

.gallery__like {
  font-weight: 700;
}
